var consequence = require('./consequentInfo.js')
var loadData = require('../../../utils/loadData.js')

function getDataUrl(zenodoId){
  return new Promise(function(resolve1, reject1){
    var zenodoFilesData = {}
    return new Promise(function(resolve2, reject2){
        


        let recordId = zenodoId.split('.')[1]; 
        console.log("The zenodo URL is " + zenodoId + " " + recordId);
    
        if (!recordId || !recordId.match(/^[0-9]+$/)){
            resolve2([], []);
        }
        
        let thePromises = []; let filenames = []
        let url = "https://zenodo.org/api/records/" + recordId

        // console.log(url)
        getFromLocation(url).then(function(data){
            console.log("The data from zenodo is ")
            console.log(data.data)
           if (data.hasOwnProperty('data') && data.data.hasOwnProperty('files') && data.data.files.length > 0){
              for (let i = 0; i < data.data.files.length; i++) {
                
                if (data.data.files[i].hasOwnProperty('links') && data.data.files[i].links.hasOwnProperty('download')){
                  console.log("The second url is ")
                  console.log( data.data.files[i].links.download )
                  console.log(data.data.files[i].filename)

                  thePromises.push(getFromLocation(data.data.files[i].links.download)) 

                  filenames.push(data.data.files[i].filename)
                }

                if (i == data.data.files.length - 1){
                  resolve2({thePromises: thePromises, filenames: filenames})
                }
              }
            }
            else {
              resolve2(thePromises, filenames)
            }
        })
    }).then (function(obj){
      return new Promise(function (resolve3, reject3){
        console.log('zenodo filename ')
        console.log(obj.filenames)

        if (!obj.hasOwnProperty('thePromises') || obj.thePromises == null || obj.thePromises == undefined || obj.thePromises.length == 0){
          resolve3()
        }
        Promise.all(obj.thePromises).then(function(fileData){
          // rearrangeFilesIfReq(fileData, obj.filenames)
          let thePromises2 = []
          for (let i =0; i<fileData.length; i++){
            
            console.log("The zenodo data2 is ")
            console.log(fileData[i])
            if (obj.filenames[i].match(/\.json$/) && typeof (fileData[i].data) === 'string'){
              alert('File in zenodo: ' + obj.filenames[i] + ' is not valid JSON.')
            }
            else if (typeof (fileData[i].data) === 'object'){
              thePromises2.push(addToTotalData(zenodoFilesData, fileData[i], obj.filenames[i]))
            }
            
            if (i == fileData.length - 1) {
              resolve3(thePromises2)
            }
          }
      })

        /* 
        console.log("Combined zenodo data is ")
        console.log(zenodoFilesData)
        resolve1({data: zenodoFilesData})
        */
      })
    })
    .then (function (){
      console.log("Combined zenodo data is ")
      console.log(zenodoFilesData)
      resolve1({data: zenodoFilesData})
    })
  })
}


function addToTotalData(allZenodoData, zenodoFileData, filename){
  return new Promise (function (resolve, reject){
    if (zenodoFileData.hasOwnProperty('data')){
      propagateSeqHashIfPres(zenodoFileData).then(function(){
        let keys = Object.keys(zenodoFileData.data);
        for (let i = 0; i < keys.length; i++){
          if (keys[i] == 'About'){
            if (!allZenodoData.hasOwnProperty('About')){
              allZenodoData['About'] = {}
            }
            
            if (zenodoFileData.data[keys[i]].hasOwnProperty('Title')){
              if (!allZenodoData['About'].hasOwnProperty('Title')){
                allZenodoData['About']['Title'] = filename + ": " + zenodoFileData.data[keys[i]]['Title']
              }
              else {
                allZenodoData['About']['Title'] = allZenodoData['About']['Title'] + '<br>' +  filename + ": " + zenodoFileData.data[keys[i]]['Title']
              }
            }
             

            if (zenodoFileData.data[keys[i]].hasOwnProperty('Description')){
              if (!allZenodoData['About'].hasOwnProperty('Description')){
                allZenodoData['About']['Description'] = filename + ": " + zenodoFileData.data[keys[i]]['Description']
              }
              else {
                allZenodoData['About']['Description'] = allZenodoData['About']['Description'] + '<br>' + filename + ": " + zenodoFileData.data[keys[i]]['Description']
              }
            }

            if (zenodoFileData.data[keys[i]].hasOwnProperty('DOI')){
              if (!allZenodoData['About'].hasOwnProperty('DOI')){
                allZenodoData['About']['DOI'] = filename + ": " + zenodoFileData.data[keys[i]]['DOI']
              }
              else{
                allZenodoData['About']['DOI'] = allZenodoData['About']['DOI']  + '<br>' + filename + ": " + zenodoFileData.data[keys[i]]['DOI']
              }
            }

            if (zenodoFileData.data[keys[i]].hasOwnProperty('Source')){
              if (!allZenodoData['About'].hasOwnProperty('Source')){
                allZenodoData['About']['Source'] = filename + ": " + zenodoFileData.data[keys[i]]['Source']
              }
              else{
                allZenodoData['About']['Source'] = allZenodoData['About']['Source'] + '<br>' + filename + ": " + zenodoFileData.data[keys[i]]['Source']
              }
            }
            
          }
          else if (keys[i] == 'Sequence_hash'){
            /* if (!allZenodoData.hasOwnProperty('About')){
              allZenodoData[keys[i]] = JSON.parse(JSON.stringify(zenodoFileData.data[keys[i]]))
            } */ 
          }
          else {
            var filename_v1 = filename.replace('.json', '') 
            allZenodoData[filename_v1 + '.' + keys[i]] = JSON.parse(JSON.stringify(zenodoFileData.data[keys[i]]))
          }
  
          if (i == keys.length -1 ){
            console.log("Attention crew, this is your captain:")
            console.log(allZenodoData)
            resolve()
          }
        }
      })
      
      
    }
    else {
      resolve()
    }
  })
}

var axios = require('axios')
// const { resolve } = require('core-js/fn/promise')
function getFromLocation (url) {
  return new Promise(function (resolve, reject) {
    const featuresFromExtServer = {}

    axios({
      method: 'get',
      url: url
    })
      .then(function (response) {
        // handle success
        // console.log("Success");
        // console.log(response.data);
        featuresFromExtServer.status = 'success'
        featuresFromExtServer.data = response.data
        resolve(featuresFromExtServer)
      })
      .catch(function (error) {
        // handle error
        console.log('Error')
        console.log(error)

        featuresFromExtServer.status = 'error'
        resolve(featuresFromExtServer)
      })
  })
}

function propagateSeqHashIfPres(addedFeatData){
  return new Promise(function(resolve, reject){
    let seqHash = '';
    if (addedFeatData.hasOwnProperty('About') && addedFeatData.About.hasOwnProperty('Sequence_hash')) {
      seqHash = addedFeatData.About.Sequence_hash; 
    }
    else {
      resolve()
    }

    let keys = Object.keys(addedFeatData); 

    if (keys.length == 0){
      resolve()
    }

    for (let i = 0; i < keys.length; i++){
      if (addedFeatData[keys[i]] !== 'About'){
        if (!addedFeatData[keys[i]].hasOwnProperty('Sequence_hash')){
          addedFeatData[keys[i]]['Sequence_hash'] = seqHash; 
        }
      }

      if (i == keys.length - 1){
        resolve()
      }
    }

    // console.log("Object keys length " + Object.keys(addedFeatData).length);
  })
}

function addColorIfMissing(aFeatSet){
  //console.log("A featset is ")
  //console.log(aFeatSet)

  let defaultColor =  '#F73C3C';
  if (!aFeatSet.hasOwnProperty('Color')){
    aFeatSet['Color'] = defaultColor;
  }
}

function extractVariantResidues(zenodoFileData, variantResidues, checkAndPushToArr, addNewResAndGrantham, addThinSpaces, selOldAaBasedOnIsOne, Store, addToVariantResidues){
  return new Promise(function(resolve, reject){

    // let variantResidues = {}; 
    let posCounter =0;
    
    let featSets = Object.keys(zenodoFileData)
    if (featSets.length == 0){
      resolve()
    }
    for (let i=0; i < featSets.length; i ++){
      // console.log("HELLO WORLD! ")
      // console.log(zenodoFileData[featSets[i]])
      if (zenodoFileData[featSets[i]].hasOwnProperty('Features')){
        // Loop through each feature
        for (let j=0; j < zenodoFileData[featSets[i]]['Features'].length; j++){
          let featName = zenodoFileData[featSets[i]]['Features'][j].Name
          let defaultDesc = '';
          if (zenodoFileData[featSets[i]]['Features'][j].hasOwnProperty('Description') && zenodoFileData[featSets[i]]['Features'][j]){
            defaultDesc = zenodoFileData[featSets[i]]['Features'][j].Description
          }
          var consInfoRes = {}; let isToAdd = true
          try {
            consInfoRes = consequence.getConsInfo(featName)
          }
          catch (err) {
            console.log("No variant here " + err)
            isToAdd = false 
          }

            // featAndDesc.featStr = consInfoRes.theOrigFeatStr;
          if (isToAdd == true && consInfoRes.hasOwnProperty('newResidues') && consInfoRes.hasOwnProperty('theOrigFeatStr') && Object.keys(consInfoRes.newResidues).length > 0){

            addColorIfMissing(zenodoFileData[featSets[i]]['Features'][j])

            consInfo = consInfoRes.newResidues;
            // console.log('Feature name ' + featName + ' ' + consInfoRes.theOrigFeatStr)


            
            for (const resNum in consInfo) {
              if (consInfo[resNum].hasOwnProperty('consStr')){
                // console.log("We do not want unknown consequence " + consInfo[resNum].consStr)
              }

              let theOrigFeatStr = addThinSpaces(consInfoRes.theOrigFeatStr, consInfo[resNum].consStr)
              let newAas = []; let isOne = true; let consequent = ''; 
              if (consInfo[resNum].hasOwnProperty('newAas')) {
                newAas = consInfo[resNum].newAas.slice()
              }
              if (consInfoRes.hasOwnProperty('isOne')) {
                isOne = consInfoRes.isOne
              }
              /*
              if (consInfoRes.hasOwnProperty('defaultDesc')) {
                defaultDesc = aFeature.Description
              }
              */
              if (consInfo[resNum].hasOwnProperty('consStr')){
                consequent = consInfo[resNum].consStr
              }
      
              let theIncr = addToVariantResidues(variantResidues, resNum, theOrigFeatStr, posCounter, newAas, isOne, defaultDesc, consequent); 
              posCounter = posCounter + theIncr;

              
              if (consInfo[resNum].hasOwnProperty('oldRes')) {
                variantResidues[resNum].oldAa = consInfo[resNum].oldRes
              }
              addNewResAndGrantham(variantResidues[resNum], consInfo[resNum])
           
              // Renaming for identification in feature set.   
              if (consInfo[resNum].hasOwnProperty('oldRes') && consInfoRes.hasOwnProperty('isOne') && consInfo[resNum].hasOwnProperty('newAas')){


                let featName = theOrigFeatStr // zenodoFileData[featSets[i]]['Features'][j].Name 
                zenodoFileData[featSets[i]]['Features'][j].Name = "<b><span id='urlVariant' class='urlVariant'>" + featName + "</span></b><span id='span_missenseHeading' class='pAaColor'> (<span id='oldAa'>" + selOldAaBasedOnIsOne(consInfo[resNum].oldRes, consInfoRes.isOne)  + '</span> &#8201;&#8594;&#8201; <span id="newAa">'
                if (consInfo[resNum].newAas[0] != 'X'){
                  zenodoFileData[featSets[i]]['Features'][j].Name = zenodoFileData[featSets[i]]['Features'][j].Name + selOldAaBasedOnIsOne(consInfo[resNum].newAas[0], consInfoRes.isOne)
                }
                zenodoFileData[featSets[i]]['Features'][j].Name = zenodoFileData[featSets[i]]['Features'][j].Name + '</span>)</span>'
              } 
              else {
                zenodoFileData[featSets[i]]['Features'][j].Name = zenodoFileData[featSets[i]]['Features'][j].Name + " <span id='span_missenseHeading' class='pAaColor'> </span> "
              }
              zenodoFileData[featSets[i]]['Features'][j].Name = zenodoFileData[featSets[i]]['Features'][j].Name + " <span id='consInfo' class='pAaColor'> " + consInfo[resNum].consStr + '</span>'
             

              // variantResidues[resNum].defaultDesc = aFeature.Description
             

            }
          }
          /* }
          catch (err) {
            console.log('Catching err in handleZenodo: ' + err)
            
          }  */
          




    
        }
      }
      
      if (i == featSets.length - 1){ 
        // Check structure counts
        let pdb = ''; let chain = '';



        // console.log("The protein primary accession is " + window.AQUARIA.protein_primary_accession)
        loadData.getTheBestStructureForVar2(pdb, chain, window.AQUARIA.protein_primary_accession, variantResidues).then(function(varStructs){
        // loadData.getTheBestStructureForVar2('7lab', 'A', 'P0DTC2', variantResidues).then(function(varStructs){
          // console.log('THE VAR STRUCTS ARE ')
          if (varStructs && varStructs.hasOwnProperty('variantStructs')){
            // console.log(varStructs)
            Store.commit('setvariantStructs', varStructs)
          }
        })
        // console.log()
        resolve()
      }
    }
  })

}
